<template>
  <div class="contantLogin">
    <div class="elTabBox">
      <el-tabs v-model="changeValue" @tab-click="handleClick">
        <el-tab-pane label="注冊" name="first"></el-tab-pane>
        <el-tab-pane label="密碼登錄" name="second"> </el-tab-pane>
      </el-tabs>
    </div>
    <div v-if="isShow">
      <div class="inputbox">
        <el-input
          placeholder="手機號或賬號"
          maxlength="11"
          v-model="loginMessage.username"
        ></el-input>
      </div>
      <div class="inputbox">
        <el-input
          placeholder="密碼"
          type="password"
          v-model="loginMessage.password"
        ></el-input>
      </div>
      <div class="forgetbox"><a href="https://www.baidu.com">忘記密碼</a></div>
      <el-button class="buttonbox" @click="loginSubmit" round type="primary"
        >登錄</el-button
      >
    </div>
    <!--注冊-->
    <div v-else>
      <div class="inputbox">
        <el-input
          placeholder="手機號"
          maxlength="11"
          v-model="registerMessage.username"
        >
          <template slot="prepend">+86</template>
        </el-input>
      </div>
      <div class="inputbox">
        <el-input
          placeholder="密碼"
          type="password"
          v-model="registerMessage.password"
        ></el-input>
      </div>
      <div class="inputbox">
        <el-input placeholder="驗證碼" v-model="registerMessage.verification">
          <el-button
            slot="append"
            @click="checkV"
            :icon="passwordCheck"
          ></el-button
        ></el-input>
      </div>

      <div class="forgetbox">
        <el-button round size="mini" @click="getV">獲取驗證碼</el-button>
      </div>
      <el-button class="buttonbox" @click="registerSubmit" round type="primary"
        >注冊</el-button
      >
    </div>
  </div>
</template>

<script>
export default {
  name: "LoginContext",
  beforeCreate() {
    console.log("navi");
  },
  data() {
    return {
      loginMessage: {
        username: "",
        password: "",
      },
      registerMessage: {
        username: "",
        password: "",
        verification: "",
      },
      changeValue: "second",
      isShow: true,
      passwordCheck: "el-icon-search",
      isCheckV: false,
    };
  },
  methods: {
    //提交登錄信息
    loginSubmit() {
      if (
        this.loginMessage.username != "" &&
        this.loginMessage.password != ""
      ) {
        this.axios({
          url: "/loginAndRegister/login",
          method: "post",
          data: {
            password: this.loginMessage.password,
            username: this.loginMessage.username,
          },
        }).then((e) => {
          if(e.data.ok){
            this.$message(e.data.message)
            let username = window.btoa(this.loginMessage.username)
            let password = window.btoa(this.loginMessage.password)
            location.href = `http://localhost:3033/?username=${username}&password=${password}`
          }else{
            this.$message(e.data.message)
          }
        });
      } else {
        this.$message("請輸入賬號或密碼");
      }
    },
    handleClick() {
      this.isShow = !this.isShow;
      console.log(this.changeValue);
    },

    //提交注冊信息
    registerSubmit() {
      if (
        this.registerMessage.password === "" ||
        this.registerMessage.username === ""
      ) {
        this.$message("請輸入賬號或密碼");
      } else {
        if (this.isCheckV === true) {
          this.$axios({
            url: "/loginAndRegister/register",
            method: "post",
            data: {
              username: this.registerMessage.username,
              password: this.registerMessage.password,
              PhoneNumber: this.registerMessage.username,
            },
          }).then((e) => {
            if (e.data.ok) {
              this.$message(e.data.message);
              this.isShow = !this.isShow;
            } else {
              this.$message(e.data.message);
            }
          });
        } else {
          this.$message("請點擊驗證碼右邊的搜索圖標進行驗證碼驗證");
        }
      }
    },
    //檢查驗證碼
    checkV() {
      if (
        this.registerMessage.verification === "" ||
        this.registerMessage.verification.length != 6
      ) {
        this.$message("請輸入正確的驗證碼信息");
      } else {
        this.$axios({
          url: "/loginAndRegister/check",
          method: "post",
          data: {
            verification: this.registerMessage.verification,
          },
        }).then((e) => {
          if (e.data.ok) {
            this.isCheckV = true;
            this.$message(e.data.message);
          } else {
            this.$message(e.data.message);
          }
        });
      }
    },
    //獲取驗證碼
    getV() {
      this.$axios({
        url: "/loginAndRegister/registerMessage",
        method: "post",
        data: {
          PhoneNumber: this.registerMessage.username,
        },
      }).then((e) => {
        if (e.data.ok) {
          this.$message(e.data.message);
        } else {
          this.$message(e.data.message);
        }
      });
    },
  },
};
</script>
<style lang="less" scoped>
.contantLogin {
  width: 30em;
  height: 30em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  border-bottom-left-radius: 50px;
  border-bottom-right-radius: 50px;
  background: #e0e0e0;
  overflow: hidden;
  box-shadow: 20px 20px 60px #bebebe, 0px -20px 60px #ffffff;
  .elTabBox {
    width: 100%;
    margin-top: 0.5em;
    margin-left: 2.5em;
  }
  .inputbox {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 23em;
    height: 4em;
  }
  .forgetbox {
    height: 1em;
    margin-top: 2%;
    display: flex;
    justify-content: end;
    align-items: center;
    a {
      font-size: 0.5em;
      color: gray;
      text-decoration: none;
    }
    button {
      font-size: 0.5em;
      color: gray;
      text-decoration: none;
    }
  }
  .buttonbox {
    margin-top: 5%;
    width: 100%;
  }
}
</style>